import { HttpClient, NonNullablePaths } from '@wix/sdk-types';
import {
  GetMemberPrivacySettingsResponse,
  MemberPrivacySettings,
  SetMemberPrivacySettingsResponse,
  getMemberPrivacySettings as universalGetMemberPrivacySettings,
  setMemberPrivacySettings as universalSetMemberPrivacySettings,
} from './members-v1-member-privacy-settings-member-privacy-settings.universal.js';

export const __metadata = { PACKAGE_NAME: '@wix/members' };

export function getMemberPrivacySettings(
  httpClient: HttpClient
): GetMemberPrivacySettingsSignature {
  return () =>
    universalGetMemberPrivacySettings(
      // @ts-ignore
      { httpClient }
    );
}

interface GetMemberPrivacySettingsSignature {
  /**
   * Retrieves the member privacy settings for a site.
   *
   * If no settings were set, the default settings are retrieved.
   *
   * >**Note:**
   * >This method requires [visitor or member authentication](https://dev.wix.com/docs/build-apps/develop-your-app/access/about-identities).
   */
  (): Promise<GetMemberPrivacySettingsResponse>;
}

export function setMemberPrivacySettings(
  httpClient: HttpClient
): SetMemberPrivacySettingsSignature {
  return (
    memberPrivacySettings: NonNullablePaths<MemberPrivacySettings, `revision`>
  ) =>
    universalSetMemberPrivacySettings(
      memberPrivacySettings,
      // @ts-ignore
      { httpClient }
    );
}

interface SetMemberPrivacySettingsSignature {
  /**
   * Sets the member privacy settings for a site.
   *
   * Each time the member privacy settings are updated, `revision` increments by 1. The existing `revision` must be included when updating the member privacy settings. This ensures you're working with the latest member privacy settings and prevents unintended overwrites.
   * @param - Member privacy settings details.
   */
  (
    memberPrivacySettings: MemberPrivacySettings
  ): Promise<SetMemberPrivacySettingsResponse>;
}

